import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from 'src/app/shared/api.service';
import { Personas } from 'src/app/classes/personas';


@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.css']
})

export class FormComponent implements OnInit {
  visible: true;

  private nombre: string;
  private apellidos: string;
  private edad: number;
  private dni: string;
  private cumple: Date;
  private color: string;
  private sexo: string;
 // private notas: string;
  userData: any[];
  _id: string;
  newUser:any[];
  //datos: any[];
  

  constructor(public apiService: ApiService, private route: ActivatedRoute, private router: Router) {
   
    var _id = this.route.snapshot.paramMap.get('_id'); 
    if (_id != null){
        this.apiService.getUser(_id).subscribe(data => {
           this.userData = data;
           
           return this.userData.forEach(userd => {
              this.nombre = userd.nombre,
              this.apellidos = userd.apellidos,
              this.edad = userd.edad,
              this.dni = userd.dni, 
              this.cumple = userd.cumple,
              this.color = userd.color,
              this.sexo = userd.sexo
     
           })           
        });
      }else{
            //this._id = null;
            this.nombre = null;
            this.apellidos = null;
            this.edad = null;
            this.dni = null;
            this.cumple = new Date();
            this.color = null;
            this.sexo = "Otro";
      }
  }

    ngOnInit() {
    //this.notas;
  }
 
  onSubmit() {
    if (this._id != null) {        
      this.apiService.updateUser(this._id, this.userData);

    } else {

      this.sexo;
     this.apiService.addUser(this.newUser[this.nombre, 
                                          this.apellidos, 
                                          this.edad, 
                                          this.dni, 
                                          this.cumple, 
                                          this.color, 
                                          this.sexo.valueOf()]).subscribe(data => {
      this.newUser = data;
     });

    } 
    this.router.navigate(['']);
    event.preventDefault();
    
  }
 
  cerrar() {
    this.router.navigate(['']);
    event.preventDefault();
  }
}
